body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none; 
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
}
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #1d1d1c;
    height: 56px;
}
.toolbar__navigation {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
}

.toolbar__logo {
    margin-left: 1rem;

    /* -ms-flex-item-align: cente */
}

.toolbar__logo a {
    color: white;
    text-decoration: none;
    font-size: 2rem;
}

.spacer{
    -webkit-flex: 1 1;
            flex: 1 1;
}
.toolbar_navigation-items li{
    padding: 0 0.5rem;

}

.toolbar_navigation-items a {
    color: white;
    text-decoration: none;
}
.toolbar_navigation-items ul{
    display:  -webkit-flex;
    display:  flex;
    list-style: none;
    margin: 0;
    padding: 0;
    padding: flex;
}

.toolbar_navigation-items ul:hover,
.toolbar_navigation-items ul:active {
    color: #fa9231;
}

@media (max-width: 768px) {
    .toolbar_navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }
    .toolbar__logo {
        margin-left: 0;
    }
}
.side-drawer {
    height: 100%;
    background-color: white;
    box-shadow: 1px 0 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-101%);
            transform: translateX(-101%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0
}

.side-drawer a {
    color: #521751;
    text-combine-upright: none;
    font-size: 1.2rem;
    text-decoration: none;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: #fa923f;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.3);
    z-index: 100;
}

